footer {
  background-color: $color-primary;
  color: $color-ordinary-dark;
}

.footer-box {
  padding-top: 48px;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  padding-bottom: 32px;
  margin-bottom: 32px;
  border-bottom: 1px solid $color-ordinary-dark;
}

.siteMap {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 44px;
  &__item {
    margin-right: 48px;
    &:nth-last {
      margin-right: 0;
    }
  }
  &__link {
    &:hover {
      border-bottom: 1px solid $color-ordinary-dark;
    }
  }
}

.contact {
  font-size: 36px;
  line-height: 54px;
  &__icon {
    margin-right: 16px;
    display: inline-block;
    font-weight: 600;
    font-size: 48px;
    padding: 6px;
    vertical-align: middle
  }
  &__link {
    padding: 0;
    margin: 0;
    &:hover {
      border-bottom: 1px solid $color-ordinary-dark;
    }
  }
}

.socialMedia {
  display: flex;
  &__item {
    &:nth-of-type(2) {
      padding: 0 32px;
    }
  }
  &__img {
    width: 48px;
    height: 48px;
    &:hover {
      opacity: 0.6;
    }
  }
}

.copyright {
  display: flex;
  justify-content: space-between;
  padding-bottom: 32px;
  font-size: 16px;
  line-height: 24px;
  &__menu {
    display: flex;
    justify-content: space-between;
    gap: 0 48px;
  }
  &__link {
    &:hover {
      border-bottom: 1px solid $color-ordinary-dark;
    }
  }
}

@media screen and (max-width: 767px) {
  .footer-box {
    padding-top: 24px;
  }
  .footer-content{
    padding-bottom: 24px;
    margin-bottom: 16px;
  }
  .siteMap {
    display: none;
  }

  .contact {
    font-size: 16px;
    line-height: 24px;
    &__icon {
      font-size: 18px;
      padding: 3px;
      margin-right: 8px;
    }
    &__link {
    }
  }

  .socialMedia {
    &__item {
      &:nth-of-type(2) {
        padding: 0 8px;
      }
    }
    &__img {
      width: 24px;
      height: 24px;
      &:hover {
        opacity: 0.6;
      }
    }
  }

  .copyright {
    flex-direction: column;
    padding-bottom: 24px;

    p {
      padding-bottom: 8px;
    }
    &__menu {
      flex-direction: column;
      gap: 8px;
    }
  }
}
