header {
  background-color: $color-primary;
}

a {
  color: $color-ordinary-dark
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 12px;
}

.navbar {
  // min-height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // padding: 4px 0;

  figure {
    width: 80px;
    height: 80px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    line-height: 30px;
    gap: 25px 64px;
  }

  &__link {
    transition: 0.7s ease;
    color: $color-ordinary-dark;
    display: block;
    padding: 25px 0;
    width: 100%;

    &:hover {
      color: $color-primary;
      background-color: $color-ordinary-dark;
    }
  }
}

.hamburger {
  display: none;
  cursor: pointer;

  &__bar {
    display: block;
    width: 30px;
    height: 4px;
    margin: 4px auto;
    background-color: $color-ordinary-dark;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
}

@media screen and (max-width: 767px) {
  .hamburger {
    display: block;
  }
  .hamburger.active .hamburger__bar {
    &:nth-child(2) {
      opacity: 0;
    }
    &:nth-child(1) {
      transform: translateY(8px) rotate(45deg);
    }
    &:nth-child(3) {
      transform: translateY(-8px) rotate(-45deg);
    }
  }

  .navbar {
    &__menu {
      position: fixed;
      left: -100%;
      top: 80px;
      gap: 0;
      flex-direction: column;
      background-color: $color-primary;
      width: 100%;
      text-align: center;
      transition: 0.3s;
    }

    &__item {
      width: 100%;
      border: 1px solid $color-ordinary-dark;
    }
  }
  .navbar__menu.active {
    left: 0;
  }
}
